<template>
  <div class="goods">
    <div class="head">
      <span class="back" @click="back()">
        <van-icon name="arrow-left" size="18px"/>
      </span>

      <span class="title">影片详情</span>
    </div>
    <van-swipe class="goods-swipe" :autoplay="3000">
      <van-swipe-item v-for="thumb in video.images" :key="thumb">
        <img :src="thumb">
      </van-swipe-item>
    </van-swipe>

    <van-cell-group>
      <van-cell>
        <div class="goods-title">{{ video.title }}</div>
        <div class="icon">
          <span>
            <van-icon name="like" color="#F00"/>
            {{video.like}}
          </span>
          <span>
            <van-icon name="fire" color="darkred"/>
            {{video.view}}
          </span>
        </div>
      </van-cell>
    </van-cell-group>
    <!-- <video controls>
      <source :src="video.video" type="video/mp4">
    </video>-->

    <video
      autoplay="autoplay"
      controls="controls"
      :src="video.video"
    ></video>
    <div class="footer">
      <span>版权所有 Copyright © 2019</span>
    </div>
  </div>
</template>

<script>
import { Icon, Cell, CellGroup, Swipe, SwipeItem, Button } from "vant";

export default {
  /* eslint-disable */
  components: {
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem
  },

  data() {
    return {
      video: {
        title: "--",
        images: [],
        video: ""
      }
    };
  },
  mounted() {
    this.getVideo(this.$route.params.id);
  },
  methods: {
    getVideo(id) {
      let sql = `select * from video where id=${id}`;
      console.log(sql);
      this.query(sql).then(res => {
        if (res.length == 1) {
          let video = res[0];
          video["images"] = video["images"].split(",");
          this.video = video;
        }
      });
    },
    back(){
        this.$router.go(-1);//返回上一层
    },
  }
};
</script>

<style lang="less">
.goods {
  .head {
    width: 100%;
    height: 55px;
    line-height: 55px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background: #fff;
    span.title {
      font-size: 16px;
      margin-left: 20px;
    }
    span.back {
      font-size: 1620pxpx;
      padding: 5px 10px;
    }
  }
  //   padding-bottom: 50px;
  video {
    width: 100%;
  }
  &-swipe {
    margin-top: 55px;
    img {
      width: 100%;
      height: 67vw;
    }
  }

  &-title {
    font-size: 16px;
  }

  &-price {
    color: #f44;
  }

  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }

  &-cell-group {
    margin: 15px 0;

    .van-cell__value {
      color: #999;
    }
  }

  &-tag {
    margin-left: 5px;
  }
}

.footer {
  margin-top: 15px;
  height: 60px;
  line-height: 60px;
  background: #fff;
  border-top: 1px solid #c5c5c5;
  text-align: center;
  span {
    font-size: 14px;
    color: #989797;
  }
}
</style>